	module.exports = {"data":{"post":{"timeToRead":2,"canonicalURL":"https://blog.iansinnott.com/the-importance-of-community/","html":"<p>Specifically, when making technical decisions.</p>\n<p>Community his hugely important any many areas of life, but lately I've found that it's particularly pertinent in making technical decisions.</p>\n<h2 id=\"an-example\"><a href=\"#an-example\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>An example</h2>\n<p>Recently at <a href=\"https://www.trustar.co/\">Trustar</a> we started using a <a href=\"https://en.wikipedia.org/wiki/Graph_database\">graph database</a>. We have a lot of interrelated data so the graph model has made conceptual sense for a long time.  So this year when we decided to implement a graph database in production we had a technical decision to make: Which database do we use?</p>\n<p>We're a small team (currently ~5 engineers) so we definitely don't have the bandwidth to build our own implementation. That means we need to choose among the existing solutions. There are currently several graph database providers in the wild, and they all seem to do pretty much the same thing. Technically speaking, the usual points they hit in differentiating themselves are ones of how they handle scalability and replication.</p>\n<!-- more -->\n<p>But technical points are not the only ones to consider when making a decision like this. We also wanted a strong community around the company we chose. Being in San Francisco, it should come as no surprise that one of these companies窶年eo4j窶馬ot only has an office hear but they also host <a href=\"http://www.meetup.com/graphdb-sf/\">a meetup</a> near our office.</p>\n<p>I went to their meetup once and was quite happy with what I found. Everyone on their team was friendly and helpful. That was all we needed. Now we're running Neo4j in production and it's the backing data-store on top of our visualization product, Constellation:</p>\n<p><img src=\"https://dropsinn.s3.amazonaws.com/Screen%20Shot%202016-04-29%20at%2011.42.03%20AM.png\" alt=\"Constellation Demo\"></p>\n<p>On top of that, I also got a chance to do an interview with the Neo4j which is now live on their blog: <a href=\"http://neo4j.com/blog/ian-sinnott-software-engineer-trustar/\">http://neo4j.com/blog/ian-sinnott-software-engineer-trustar/</a></p>\n<p>Then earlier this week two Neo4j team members came to our office and had a long discussion with us about our graph database architecture. All of this while <strong>we're still on the <em>free teir</em> of their product</strong>. That's excellent.</p>\n<h2 id=\"community-matters\"><a href=\"#community-matters\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Community Matters</h2>\n<p>That was just one example in my own life, but if you look at other projects with significant communities the same is clear:</p>\n<ul>\n<li>React\nReact is some amazing tech, but imagine how much <em>less</em> useful it would be without the plethora of learning materials, examples and premade components all created by the community?</li>\n<li>Redux\nSame as above. Redux is an excellent state-management solution on it's own but how much <em>less</em> useful would it be without the fanatic community that surrounds it?</li>\n</ul>\n<p>The point being, if you're ever struggling with a decision between two technical solutions opt for the one with the larger community.</p>","frontmatter":{"created":"2016-04-29T07:00:00.000Z","title":"The Importance of Community","disqusId":null}}},"pathContext":{"id":"/Users/jandrix/dev/sites/blog.iansinnott.com/content/_posts/2016-04-29-the-importance-of-community.md absPath of file >>> MarkdownRemark","next":{"frontmatter":{"title":"Testing Webpack Plugins","created":"2016-05-11T07:00:00.000Z"},"slug":"testing-webpack-plugins"},"prev":{"frontmatter":{"title":"Going fully HTTPS (SSL) fo' free","created":"2016-04-29T07:00:00.000Z"},"slug":"going-fully-https-fo-free"}}}

